.homepage-container {
    display: -ms-flexbox;
    display: flex;
    margin: 1rem 0;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.homepage-side {
    -ms-flex: 0 0 20%;
        flex: 0 0 20%;

} 
.homepage-main {
    -ms-flex: 0 0 78%;
        flex: 0 0 78%;
}

.homepage-main .place-ad-button {
    display: none;
}

.listings {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    padding: 4px;
}

@media screen and (max-width: 760px) {
    .homepage-side {
        display: none;
    }
    
    .homepage-main {
        -ms-flex: 0 0 100%;
            flex: 0 0 100%;
    }
    .homepage-main .place-ad-button {
        display: block;
        margin: 2rem auto;
    }
}


